:root {
}

body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica", sans-serif;
}

.page {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100vh;
  background: #233445;
}

.average {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.weightMain {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: #efefef;
}

.weightInput {
  padding: 4px;
  background: red;
}

.weightValues {
  background: white;
  color: #202020;
}

.weight {
  color: #080808;
  margin-right: 8px;
}

.time {
  color: #404040;
}

